import React from 'react';
import './App.css';
import text from './input.txt'
const maxLength = 20

class App extends React.Component {
    constructor() {
      super()
      this.state = {
        wordN: 0,
        word: '',
        text: ''
      }
      this.changeWord = this.changeWord.bind(this)
      fetch(text)
        .then(r => r.text())
        .then(text => {
          this.setState({
            text: text.split('\n'),
            word: text.split('\n')[0].padEnd(maxLength, '$')
          })
        });
    }
    changeWord() {
      this.setState(prevState => {
        return {
          wordN: prevState.wordN + 1,
          word: prevState.wordN+2 === prevState.text.length ? 
          (prevState.text[prevState.wordN + 1]+'$$$$$Prosím vystúpte.$$$$$').padEnd(maxLength, '$'):
          prevState.wordN+1 >= prevState.text.length ? 'Koniec programu$$$$$' :
          prevState.text[prevState.wordN + 1].padEnd(maxLength, '$')
        }
      })
    }
    componentDidMount() {
      this.timer = setInterval(() => {
        this.setState(prevState => {
          return {
            word: prevState.word.slice(1) + prevState.word.slice(0,1)
          }
        })
      }, 200)
      document.addEventListener("keydown", this.changeWord, false)
    }
    render() {
      return ( 
      <div className='cont'> 
        <div className='display'>
        {this.state.word.replaceAll('$','\u00A0').slice(0,maxLength)} 
        </div>
        </div>
        )
      }
    }

    export default App;